import { DestroyRef, Injectable, inject } from '@angular/core';
import { RealSystemHealthService } from '../../../core/services/real-system-health.service';
import { SystemHealthBroadcastService } from './system-health-broadcast.service';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { SystemHealthData } from '../models';

@Injectable({
  providedIn: 'root',
})
export class SystemHealthService {
  private readonly realService = inject(RealSystemHealthService);
  private readonly broadcastService = inject(SystemHealthBroadcastService);
  private readonly destroyRef = inject(DestroyRef);

  readonly systemHealth$ = this.realService.systemHealth$;
  currentHealthData: SystemHealthData | null = null;

  initializeHealthListeners() {
    // Initialize the real service (WebSocket connection + initial data fetch)
    this.realService.initialize();

    // Listen to health data updates
    this.systemHealth$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((data) => {
        this.currentHealthData = data;
      });
  }

  refreshData(timeRange?: string): void {
    this.realService.refreshData(timeRange);
  }
}
