import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  inject,
  OnInit,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { SelectChangeEvent, SelectModule } from 'primeng/select';
import { ButtonModule } from 'primeng/button';
import { CardModule } from 'primeng/card';
import { ChartModule } from 'primeng/chart';

import { RealSystemHealthService } from '../../../../../core/services/real-system-health.service';
import { ThroughputSummary, ThroughputMetrics } from '../../../models';

@Component({
  selector: 'app-event-flow',
  imports: [
    CommonModule,
    FormsModule,
    CardModule,
    ChartModule,
    ButtonModule,
    SelectModule,
  ],
  templateUrl: './event-flow.component.html',
  styleUrl: './event-flow.component.scss',
})
export class EventFlowComponent implements OnInit {
  private readonly healthService = inject(RealSystemHealthService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);

  // Data
  throughput: ThroughputSummary | null = null;

  // Loading states
  loading = false;

  // Chart data
  chartData: unknown;
  chartOptions: unknown;

  // Time range selector
  timeRanges = [1, 6, 12, 24]; // Hours
  timeRangeOptions = this.timeRanges.map((range) => ({
    label: range + 'h',
    value: range,
  }));
  selectedTimeRange = 6; // Default to 6 hours

  ngOnInit() {
    this.initializeChartOptions();
    this.fetchThroughputData();
  }

  private fetchThroughputData(): void {
    this.loading = true;
    this.healthService
      .fetchEventFlow(`${this.selectedTimeRange}h`)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: (data: ThroughputSummary | null) => {
          this.throughput = data;
          this.updateChartData();
          this.loading = false;
          this.cdr.detectChanges();
        },
        error: () => {
          this.loading = false;
          this.cdr.detectChanges();
        },
      });
  }

  private initializeChartOptions(): void {
    const documentStyle = getComputedStyle(document.documentElement);
    const textColor = documentStyle.getPropertyValue('--p-text-color');
    const textColorSecondary = documentStyle.getPropertyValue(
      '--p-text-muted-color',
    );
    const surfaceBorder = documentStyle.getPropertyValue(
      '--p-content-border-color',
    );

    this.chartOptions = {
      maintainAspectRatio: false,
      aspectRatio: 0.6,
      plugins: {
        legend: {
          labels: {
            color: textColor,
          },
        },
        tooltip: {
          mode: 'index',
          intersect: false,
        },
      },
      scales: {
        x: {
          ticks: {
            color: textColorSecondary,
          },
          grid: {
            color: surfaceBorder,
          },
        },
        y: {
          type: 'linear',
          display: true,
          position: 'left',
          ticks: {
            color: textColorSecondary,
          },
          grid: {
            color: surfaceBorder,
          },
          title: {
            display: true,
            text: 'Operations per Second',
            color: textColor,
          },
        },
      },
    };
  }

  private updateChartData(): void {
    if (!this.throughput?.dataPoints || this.throughput.dataPoints.length === 0)
      return;

    const labels = this.throughput.dataPoints.map((dp: ThroughputMetrics) =>
      new Date(dp.timestamp).toLocaleTimeString([], {
        hour: '2-digit',
        minute: '2-digit',
      }),
    );

    const documentStyle = getComputedStyle(document.documentElement);

    this.chartData = {
      labels,
      datasets: [
        {
          label: 'Requests/sec',
          data: this.throughput.dataPoints.map(
            (dp: ThroughputMetrics) => dp.requestsPerSecond,
          ),
          fill: false,
          borderColor: documentStyle.getPropertyValue('--p-blue-500'),
          backgroundColor: documentStyle.getPropertyValue('--p-blue-500'),
          tension: 0.4,
        },
        {
          label: 'Events/sec',
          data: this.throughput.dataPoints.map(
            (dp: ThroughputMetrics) => dp.eventsPerSecond,
          ),
          fill: false,
          borderColor: documentStyle.getPropertyValue('--p-green-500'),
          backgroundColor: documentStyle.getPropertyValue('--p-green-500'),
          tension: 0.4,
        },
        {
          label: 'Dependencies/sec',
          data: this.throughput.dataPoints.map(
            (dp: ThroughputMetrics) => dp.dependencyCallsPerSecond,
          ),
          fill: false,
          borderColor: documentStyle.getPropertyValue('--p-orange-500'),
          backgroundColor: documentStyle.getPropertyValue('--p-orange-500'),
          tension: 0.4,
        },
      ],
    };
  }

  onTimeRangeChange(event: SelectChangeEvent): void {
    this.selectedTimeRange = +event.value;
    this.fetchThroughputData();
  }

  refreshData(): void {
    this.fetchThroughputData();
  }
}
