import { Injectable, inject, DestroyRef } from '@angular/core';
import { BehaviorSubject, Subject, timer } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

export interface WebSocketMessage {
  type: string;
  [key: string]: unknown;
}

export interface WebSocketConfig {
  url: string;
  reconnectInterval?: number;
  reconnectAttempts?: number;
  heartbeatInterval?: number;
}

@Injectable({
  providedIn: 'root',
})
export class WebSocketService {
  private ws: WebSocket | null = null;
  private config: WebSocketConfig | null = null;
  private reconnectAttempt = 0;
  private readonly messageSubject = new Subject<WebSocketMessage>();
  private readonly statusSubject = new BehaviorSubject<boolean>(false);
  private readonly destroyRef = inject(DestroyRef);

  readonly messages$ = this.messageSubject.asObservable();
  readonly status$ = this.statusSubject.asObservable();

  connect(config: WebSocketConfig): void {
    this.config = {
      reconnectInterval: 5000,
      reconnectAttempts: 10,
      heartbeatInterval: 30000,
      ...config,
    };

    this.createConnection();
  }

  disconnect(): void {
    if (this.ws) {
      this.ws.close();
      this.ws = null;
      this.statusSubject.next(false);
    }
  }

  send(message: WebSocketMessage): void {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify(message));
    } else {
      console.error('WebSocket is not connected');
    }
  }

  subscribe(topics: string[]): void {
    this.send({
      type: 'subscribe',
      topics,
    });
  }

  private createConnection(): void {
    if (!this.config) return;

    try {
      this.ws = new WebSocket(this.config.url);

      this.ws.onopen = () => {
        this.statusSubject.next(true);
        this.reconnectAttempt = 0;

        // Subscribe to topics after connection
        this.subscribe(['health', 'metrics', 'alerts']);
      };

      this.ws.onmessage = (event: MessageEvent) => {
        try {
          const message = JSON.parse(event.data) as WebSocketMessage;
          this.messageSubject.next(message);
        } catch (error) {
          console.error('Error parsing WebSocket message:', error);
        }
      };

      this.ws.onerror = (error) => {
        console.error('WebSocket error:', error);
      };

      this.ws.onclose = () => {
        this.statusSubject.next(false);
        this.attemptReconnect();
      };
    } catch (error) {
      console.error('Error creating WebSocket connection:', error);
      this.attemptReconnect();
    }
  }

  private attemptReconnect(): void {
    if (!this.config) return;

    const maxAttempts = this.config.reconnectAttempts ?? 10;
    if (this.reconnectAttempt >= maxAttempts) {
      console.error('Max reconnection attempts reached');
      return;
    }

    this.reconnectAttempt++;

    const reconnectDelay = this.config.reconnectInterval ?? 5000;
    timer(reconnectDelay)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        this.createConnection();
      });
  }

  isConnected(): boolean {
    return this.ws?.readyState === WebSocket.OPEN;
  }
}
