import { Injectable, inject, DestroyRef } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import {
  BehaviorSubject,
  Observable,
  catchError,
  of,
  lastValueFrom,
} from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { WebSocketService, WebSocketMessage } from './websocket.service';
import { SystemHealthBroadcastService } from '../../features/administration/services/system-health-broadcast.service';
import {
  SystemHealthData,
  SystemOverview,
  ServiceHealthDetail,
  FunctionPerformance,
  ThroughputSummary,
  StorageSummary,
  AlertsSummary,
  ErrorTimelineSummary,
  HealthStatus,
  FunctionTimelineSummary,
  FunctionDependencySummary,
  FunctionResourceSummary,
} from '../../features/administration/models';
import { environment } from '../../../environments/environment';
import { isDefined } from '../../shared/utils';

@Injectable({
  providedIn: 'root',
})
export class RealSystemHealthService {
  private readonly http = inject(HttpClient);
  private readonly wsService = inject(WebSocketService);
  private readonly broadcastService = inject(SystemHealthBroadcastService);
  private readonly destroyRef = inject(DestroyRef);

  private readonly healthDataSubject =
    new BehaviorSubject<SystemHealthData | null>(null);
  readonly systemHealth$ = this.healthDataSubject.asObservable();

  private initialized = false;

  initialize(): void {
    if (this.initialized) return;
    this.initialized = true;

    // Connect to WebSocket
    this.wsService.connect({
      url: environment.wsUrl,
      reconnectInterval: 5000,
      reconnectAttempts: 10,
    });

    // Listen to WebSocket status
    this.wsService.status$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((connected) => {
        this.broadcastService.broadcastWebSocketStatus(
          connected,
          connected ? 'Connected' : 'Disconnected',
        );

        // Fetch initial data when connected
        if (connected) {
          void this.fetchAllHealthData();
        }
      });

    // Listen to WebSocket messages
    this.wsService.messages$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((message) => {
        this.handleWebSocketMessage(message);
      });
  }

  private handleWebSocketMessage(message: WebSocketMessage): void {
    switch (message.type) {
      case 'health_update':
        // Handle real-time health updates
        if (message['data'] != null) {
          this.updateHealthData(message['data'] as Partial<SystemHealthData>);
        }
        break;

      case 'service_alert':
        // Handle service alerts
        this.broadcastService.broadcastServiceAlert(
          message['serviceName'] as string,
          message['severity'] as 'warning' | 'error' | 'critical',
          message['message'] as string,
        );
        break;

      default:
        break;
    }
  }

  private async fetchAllHealthData(timeRange?: string): Promise<void> {
    try {
      const [serviceDetails, functions, storage, throughput, alerts] =
        await Promise.all([
          lastValueFrom(this.fetchServiceDetails(timeRange)),
          lastValueFrom(this.fetchFunctions(timeRange)),
          lastValueFrom(this.fetchStorageMetrics(timeRange)),
          lastValueFrom(this.fetchThroughputMetrics(timeRange)),
          lastValueFrom(this.fetchRecentAlerts(timeRange)),
        ]);

      // Convert backend ServiceHealthDetail[] to SystemOverview structure
      const overview: SystemOverview = {
        services: serviceDetails ?? [],
        overallHealth: this.calculateOverallHealth(serviceDetails ?? []),
        lastUpdated: new Date().toISOString(),
      };

      const healthData: SystemHealthData = {
        overview,
        timeline: [],
        azureFunctions: functions ?? [],
        eventFlow: throughput ?? {
          timeRange: timeRange ?? '1h',
          averageRequestsPerSecond: 0,
          averageEventsPerSecond: 0,
          averageDependenciesPerSecond: 0,
          peakRequestsPerSecond: 0,
          peakEventsPerSecond: 0,
          totalRequests: 0,
          totalEvents: 0,
          totalDependencies: 0,
          dataPoints: [],
        },
        storage: storage ?? {
          timeRange: timeRange ?? '1h',
          totalOperations: 0,
          totalSuccessRate: 0,
          storageServices: [],
        },
        alerts: alerts ?? {
          timeRange: timeRange ?? '1h',
          totalAlerts: 0,
          criticalCount: 0,
          errorCount: 0,
          warningCount: 0,
          informationCount: 0,
          alerts: [],
        },
      };

      this.healthDataSubject.next(healthData);
      this.broadcastService.broadcastHealthUpdate(healthData);
    } catch (error) {
      console.error('Error fetching health data:', error);
    }
  }

  private fetchServiceDetails(
    timeRange?: string,
  ): Observable<ServiceHealthDetail[] | null> {
    const queryParam =
      timeRange != null && timeRange !== '' ? `?timeRange=${timeRange}` : '';
    const url = `${environment.apiUrl}/api/health/service-details${queryParam}`;
    return this.http.get<ServiceHealthDetail[]>(url).pipe(
      catchError((error: HttpErrorResponse) => {
        console.error('Error fetching service details:', error);
        return of(null);
      }),
    );
  }

  private fetchFunctions(
    timeRange?: string,
  ): Observable<FunctionPerformance[] | null> {
    const queryParam =
      timeRange != null && timeRange !== '' ? `?timeRange=${timeRange}` : '';
    const url = `${environment.apiUrl}/api/health/functions${queryParam}`;
    return this.http.get<FunctionPerformance[]>(url).pipe(
      catchError((error: HttpErrorResponse) => {
        console.error('Error fetching functions:', error);
        return of(null);
      }),
    );
  }

  private fetchStorageMetrics(
    timeRange?: string,
  ): Observable<StorageSummary | null> {
    const queryParam =
      timeRange != null && timeRange !== '' ? `?timeRange=${timeRange}` : '';
    const url = `${environment.apiUrl}/api/metrics/storage${queryParam}`;
    return this.http.get<StorageSummary>(url).pipe(
      catchError((error: HttpErrorResponse) => {
        console.error('Error fetching storage metrics:', error);
        return of(null);
      }),
    );
  }

  private fetchThroughputMetrics(
    timeRange?: string,
  ): Observable<ThroughputSummary | null> {
    const queryParam =
      timeRange != null && timeRange !== '' ? `?timeRange=${timeRange}` : '';
    const url = `${environment.apiUrl}/api/metrics/throughput${queryParam}`;
    return this.http.get<ThroughputSummary>(url).pipe(
      catchError((error: HttpErrorResponse) => {
        console.error('Error fetching throughput metrics:', error);
        return of(null);
      }),
    );
  }

  private fetchRecentAlerts(
    timeRange?: string,
  ): Observable<AlertsSummary | null> {
    const queryParam =
      timeRange != null && timeRange !== '' ? `?timeRange=${timeRange}` : '';
    const url = `${environment.apiUrl}/api/alerts/recent${queryParam}`;
    return this.http.get<AlertsSummary>(url).pipe(
      catchError((error: HttpErrorResponse) => {
        console.error('Error fetching recent alerts:', error);
        return of(null);
      }),
    );
  }

  private fetchErrorTimeline(
    timeRange?: string,
  ): Observable<ErrorTimelineSummary | null> {
    const queryParam =
      timeRange != null && timeRange !== '' ? `?timeRange=${timeRange}` : '';
    const url = `${environment.apiUrl}/api/alerts/timeline${queryParam}`;
    return this.http.get<ErrorTimelineSummary>(url).pipe(
      catchError((error: HttpErrorResponse) => {
        console.error('Error fetching error timeline:', error);
        return of(null);
      }),
    );
  }

  private updateHealthData(update: Partial<SystemHealthData>): void {
    const currentData = this.healthDataSubject.value;
    if (currentData) {
      const updatedData = { ...currentData, ...update };
      this.healthDataSubject.next(updatedData);
      this.broadcastService.broadcastHealthUpdate(updatedData);
    }
  }

  fetchFunctionTimeline(
    timeRange?: string,
    granularity?: string,
  ): Observable<FunctionTimelineSummary | null> {
    const params = new URLSearchParams();
    if (isDefined(timeRange)) {
      params.append('timeRange', timeRange);
    }
    if (isDefined(granularity)) {
      params.append('granularity', granularity);
    }
    const queryParam = params.toString() ? `?${params.toString()}` : '';
    const url = `${environment.apiUrl}/api/health/functions/timeline${queryParam}`;
    return this.http.get<FunctionTimelineSummary>(url).pipe(
      catchError((error: HttpErrorResponse) => {
        console.error('Error fetching function timeline:', error);
        return of(null);
      }),
    );
  }

  fetchFunctionResources(
    timeRange?: string,
  ): Observable<FunctionResourceSummary | null> {
    const queryParam =
      timeRange != null && timeRange !== '' ? `?timeRange=${timeRange}` : '';
    const url = `${environment.apiUrl}/api/health/functions/resources${queryParam}`;
    return this.http.get<FunctionResourceSummary>(url).pipe(
      catchError((error: HttpErrorResponse) => {
        console.error('Error fetching function resources:', error);
        return of(null);
      }),
    );
  }

  fetchFunctionDependencies(
    timeRange?: string,
  ): Observable<FunctionDependencySummary | null> {
    const queryParam =
      timeRange != null && timeRange !== '' ? `?timeRange=${timeRange}` : '';
    const url = `${environment.apiUrl}/api/health/functions/dependencies${queryParam}`;
    return this.http.get<FunctionDependencySummary>(url).pipe(
      catchError((error: HttpErrorResponse) => {
        console.error('Error fetching function dependencies:', error);
        return of(null);
      }),
    );
  }

  refreshData(timeRange?: string): void {
    void this.fetchAllHealthData(timeRange);
  }

  getCurrentHealthData(): SystemHealthData | null {
    return this.healthDataSubject.value;
  }

  // Public methods for components to fetch specific data types
  fetchAlerts(timeRange?: string): Observable<AlertsSummary | null> {
    return this.fetchRecentAlerts(timeRange);
  }

  fetchServices(timeRange?: string): Observable<ServiceHealthDetail[] | null> {
    return this.fetchServiceDetails(timeRange);
  }

  fetchAzureFunctions(
    timeRange?: string,
  ): Observable<FunctionPerformance[] | null> {
    return this.fetchFunctions(timeRange);
  }

  fetchStorage(timeRange?: string): Observable<StorageSummary | null> {
    return this.fetchStorageMetrics(timeRange);
  }

  fetchEventFlow(timeRange?: string): Observable<ThroughputSummary | null> {
    return this.fetchThroughputMetrics(timeRange);
  }

  fetchErrorTimelineData(
    timeRange?: string,
  ): Observable<ErrorTimelineSummary | null> {
    return this.fetchErrorTimeline(timeRange);
  }

  private calculateOverallHealth(
    services: ServiceHealthDetail[],
  ): HealthStatus {
    if (services.length === 0) return 'Healthy';

    const criticalCount = services.filter(
      (s) => s.status === 'Critical',
    ).length;
    const warningCount = services.filter((s) => s.status === 'Warning').length;

    if (criticalCount > 0) return 'Critical';
    if (warningCount > 0) return 'Warning';
    return 'Healthy';
  }
}
