import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  inject,
  OnInit,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

import { SelectChangeEvent, SelectModule } from 'primeng/select';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { ButtonModule } from 'primeng/button';
import { CardModule } from 'primeng/card';
import { TableModule } from 'primeng/table';
import { TagModule } from 'primeng/tag';

import { RealSystemHealthService } from '../../../../../core/services/real-system-health.service';
import { StorageSummary } from '../../../models';

type TagSeverity =
  | 'success'
  | 'warn'
  | 'danger'
  | 'info'
  | 'secondary'
  | 'contrast'
  | null
  | undefined;

@Component({
  selector: 'app-data-storage',
  imports: [
    CommonModule,
    FormsModule,
    CardModule,
    TableModule,
    ButtonModule,
    SelectModule,
    TagModule,
  ],
  templateUrl: './data-storage.component.html',
  styleUrl: './data-storage.component.scss',
})
export class DataStorageComponent implements OnInit {
  private readonly healthService = inject(RealSystemHealthService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);

  //Data
  storage: StorageSummary | null = null;

  // Loading states
  loading = false;

  // Expanded rows tracking
  expandedRows: Record<string, boolean> = {};

  // Time range selector
  timeRanges = [6, 12, 24]; // Hours
  timeRangeOptions = this.timeRanges.map((range) => ({
    label: range + 'h',
    value: range,
  }));
  selectedTimeRange = 6; // Default to 6 hours

  ngOnInit() {
    // Initial fetch
    this.fetchStorageData();
  }

  private fetchStorageData(): void {
    this.loading = true;
    this.healthService
      .fetchStorage(`${this.selectedTimeRange}h`)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: (data) => {
          this.storage = data;
          this.loading = false;
          this.cdr.detectChanges();
        },
        error: () => {
          this.loading = false;
          this.cdr.detectChanges();
        },
      });
  }

  onTimeRangeChange(event: SelectChangeEvent): void {
    this.selectedTimeRange = +event.value;
    this.fetchStorageData();
  }

  refreshData(): void {
    this.fetchStorageData();
  }

  getStatusSeverity(status: string): TagSeverity {
    switch (status?.toLowerCase()) {
      case 'healthy':
        return 'success';
      case 'warning':
        return 'warn';
      case 'critical':
        return 'danger';
      default:
        return 'info';
    }
  }

  getStatusIcon(status: string): string {
    switch (status?.toLowerCase()) {
      case 'healthy':
        return 'pi-check-circle';
      case 'warning':
        return 'pi-exclamation-triangle';
      case 'critical':
        return 'pi-times-circle';
      default:
        return 'pi-info-circle';
    }
  }
}
