import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  ElementRef,
  inject,
  OnInit,
  ViewChild,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { forkJoin } from 'rxjs';
import {
  Chart,
  ChartConfiguration,
  registerables,
  TooltipItem,
} from 'chart.js';
import 'chartjs-adapter-date-fns';
import { RealSystemHealthService } from '../../../../../core/services/real-system-health.service';
import {
  FunctionPerformance,
  FunctionTimelineSummary,
  FunctionResourceSummary,
  FunctionDependencySummary,
  FunctionResourceMetrics,
} from '../../../models';

// PrimeNG imports
import { CardModule } from 'primeng/card';
import { TableModule } from 'primeng/table';
import { ButtonModule } from 'primeng/button';
import { SelectModule } from 'primeng/select';
import { TabsModule } from 'primeng/tabs';
import { TagModule } from 'primeng/tag';

Chart.register(...registerables);

interface SelectChangeEvent {
  value: string | number;
}

@Component({
  selector: 'app-azure-functions',
  imports: [
    CommonModule,
    FormsModule,
    CardModule,
    TableModule,
    ButtonModule,
    SelectModule,
    TabsModule,
    TagModule,
  ],
  templateUrl: './azure-functions.component.html',
  styleUrl: './azure-functions.component.scss',
})
export class AzureFunctionsComponent implements OnInit, AfterViewInit {
  private readonly healthService = inject(RealSystemHealthService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);

  @ViewChild('timelineCanvas') timelineCanvas!: ElementRef<HTMLCanvasElement>;
  @ViewChild('coldStartCanvas') coldStartCanvas!: ElementRef<HTMLCanvasElement>;

  // Data
  functions: FunctionPerformance[] = [];
  timeline: FunctionTimelineSummary | null = null;
  resources: FunctionResourceSummary | null = null;
  dependencies: FunctionDependencySummary | null = null;

  // Loading states
  loading = false;
  timelineLoading = false;
  resourcesLoading = false;
  dependenciesLoading = false;

  // Charts
  private timelineChart: Chart | null = null;
  private coldStartChart: Chart | null = null;

  // Time range selector
  timeRanges = [1, 6, 12, 24]; // Hours
  timeRangeOptions = this.timeRanges.map((range) => ({
    label: range + 'h',
    value: range,
  }));
  selectedTimeRange = 1; // Default to 1 hour

  // Granularity for timeline
  granularityOptions = [
    { label: '1 minute', value: '1m' },
    { label: '5 minutes', value: '5m' },
    { label: '15 minutes', value: '15m' },
    { label: '30 minutes', value: '30m' },
    { label: '1 hour', value: '1h' },
  ];
  selectedGranularity = '5m';

  // Expanded rows for table
  expandedRows: Record<string, boolean> = {};

  ngOnInit(): void {
    this.loadAllData();

    // Listen for real-time updates
    this.healthService.systemHealth$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((health) => {
        if (health?.azureFunctions) {
          this.functions = health.azureFunctions;
          this.cdr.detectChanges();
        }
      });
  }

  ngAfterViewInit(): void {
    // Charts will be created after data loads
    if (this.timeline) {
      this.createTimelineChart();
    }
    if (this.resources) {
      this.createColdStartChart();
    }
  }

  loadAllData(): void {
    this.loading = true;
    const timeRange = `${this.selectedTimeRange}h`;

    forkJoin({
      functions: this.healthService.fetchAzureFunctions(timeRange),
      timeline: this.healthService.fetchFunctionTimeline(
        timeRange,
        this.selectedGranularity,
      ),
      resources: this.healthService.fetchFunctionResources(timeRange),
      dependencies: this.healthService.fetchFunctionDependencies(timeRange),
    }).subscribe({
      next: (data) => {
        this.functions = data.functions ?? [];
        this.timeline = data.timeline;
        this.resources = data.resources;
        this.dependencies = data.dependencies;
        this.loading = false;
        this.cdr.detectChanges();

        // Create charts after data loads and view is ready
        if (this.timelineCanvas?.nativeElement != null) {
          this.createTimelineChart();
        }
        if (this.coldStartCanvas?.nativeElement != null) {
          this.createColdStartChart();
        }
      },
      error: (error) => {
        console.error('Error loading Azure Functions data:', error);
        this.loading = false;
        this.cdr.detectChanges();
      },
    });
  }

  onTimeRangeChange(event: SelectChangeEvent): void {
    this.selectedTimeRange = +event.value;
    this.loadAllData();
  }

  onGranularityChange(event: SelectChangeEvent): void {
    this.selectedGranularity = event.value as string;
    this.loadTimeline();
  }

  loadTimeline(): void {
    this.timelineLoading = true;
    const timeRange = `${this.selectedTimeRange}h`;

    this.healthService
      .fetchFunctionTimeline(timeRange, this.selectedGranularity)
      .subscribe({
        next: (timeline) => {
          this.timeline = timeline;
          this.timelineLoading = false;
          this.cdr.detectChanges();

          if (this.timelineCanvas?.nativeElement != null) {
            this.createTimelineChart();
          }
        },
        error: (error) => {
          console.error('Error loading timeline:', error);
          this.timelineLoading = false;
          this.cdr.detectChanges();
        },
      });
  }

  refreshData(): void {
    this.loadAllData();
  }

  getStatusSeverity(
    status: string,
  ): 'success' | 'info' | 'warn' | 'danger' | 'secondary' | 'contrast' {
    switch (status) {
      case 'Healthy':
        return 'success';
      case 'Warning':
        return 'warn';
      case 'Critical':
        return 'danger';
      case 'Unknown':
        return 'secondary';
      default:
        return 'secondary';
    }
  }

  getStatusIcon(status: string): string {
    switch (status) {
      case 'Healthy':
        return 'pi-check-circle';
      case 'Warning':
        return 'pi-exclamation-triangle';
      case 'Critical':
        return 'pi-times-circle';
      case 'Unknown':
        return 'pi-question-circle';
      default:
        return 'pi-circle';
    }
  }

  formatDuration(ms: number): string {
    if (ms < 1000) return `${Math.round(ms)}ms`;
    return `${(ms / 1000).toFixed(2)}s`;
  }

  getFunctionDependencies(functionName: string) {
    return (
      this.dependencies?.dependencies.filter(
        (d) => d.functionName === functionName,
      ) ?? []
    );
  }

  private createTimelineChart(): void {
    const canvas = this.timelineCanvas?.nativeElement;
    if (this.timeline == null || canvas == null) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    // Destroy existing chart
    if (this.timelineChart) {
      this.timelineChart.destroy();
    }

    // Colors for different functions
    const colors = [
      '#3b82f6',
      '#8b5cf6',
      '#ec4899',
      '#f59e0b',
      '#10b981',
      '#06b6d4',
      '#ef4444',
    ];

    const datasets = this.timeline.functionNames.map((funcName, index) => {
      const funcData =
        this.timeline?.dataPoints.filter(
          (dp) => dp.functionName === funcName,
        ) ?? [];

      return {
        label: funcName,
        data: funcData.map((dp) => ({
          x: new Date(dp.timestamp).getTime(),
          y: dp.executions,
        })),
        borderColor: colors[index % colors.length],
        backgroundColor: colors[index % colors.length] + '20',
        borderWidth: 2,
        tension: 0.4,
        fill: true,
      };
    });

    const config: ChartConfiguration<'line'> = {
      type: 'line',
      data: { datasets },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        interaction: {
          mode: 'index',
          intersect: false,
        },
        plugins: {
          legend: {
            position: 'bottom',
            labels: {
              usePointStyle: true,
              padding: 15,
              font: { family: 'Ubuntu' },
            },
          },
          tooltip: {
            callbacks: {
              title: (items: TooltipItem<'line'>[]) => {
                const xValue = items[0]?.parsed?.x;
                if (xValue == null || xValue === 0) return '';
                return new Date(xValue).toLocaleString();
              },
              label: (context: TooltipItem<'line'>) => {
                const label = context.dataset.label ?? '';
                const value = context.parsed.y;
                return `${label}: ${value} execution${value === 1 ? '' : 's'}`;
              },
            },
          },
        },
        scales: {
          x: {
            type: 'time',
            time: {
              unit: 'minute',
              displayFormats: {
                minute: 'HH:mm',
                hour: 'MMM d, HH:mm',
              },
            },
            title: {
              display: true,
              text: 'Time',
              font: { family: 'Ubuntu' },
            },
            grid: { display: true, color: '#f3f4f6' },
            ticks: { font: { family: 'Ubuntu' } },
          },
          y: {
            beginAtZero: true,
            title: {
              display: true,
              text: 'Executions',
              font: { family: 'Ubuntu' },
            },
            ticks: {
              precision: 0,
              font: { family: 'Ubuntu' },
            },
            grid: { color: '#f3f4f6' },
          },
        },
      },
    };

    this.timelineChart = new Chart(ctx, config);
  }

  private createColdStartChart(): void {
    const canvas = this.coldStartCanvas?.nativeElement;
    if (this.resources == null || canvas == null) return;

    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    // Destroy existing chart
    if (this.coldStartChart) {
      this.coldStartChart.destroy();
    }

    const functions = this.resources.functions
      .filter((f) => f.coldStarts > 0)
      .sort((a, b) => b.coldStartRate - a.coldStartRate)
      .slice(0, 10); // Top 10

    const config: ChartConfiguration<'bar'> = {
      type: 'bar',
      data: {
        labels: functions.map((f) => f.functionName),
        datasets: [
          {
            label: 'Cold Start Rate (%)',
            data: functions.map((f) => f.coldStartRate),
            backgroundColor: '#ef4444',
            borderColor: '#dc2626',
            borderWidth: 1,
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        indexAxis: 'y',
        plugins: {
          legend: { display: false },
          tooltip: {
            callbacks: {
              label: (context: TooltipItem<'bar'>) => {
                const func = functions[context.dataIndex];
                return [
                  `Cold Start Rate: ${func.coldStartRate}%`,
                  `Cold Starts: ${func.coldStarts}`,
                  `Total Executions: ${func.totalExecutions}`,
                  `Avg Cold Start: ${func.avgColdStartDuration}ms`,
                  `Avg Warm Start: ${func.avgWarmStartDuration}ms`,
                ];
              },
            },
          },
        },
        scales: {
          x: {
            beginAtZero: true,
            title: {
              display: true,
              text: 'Cold Start Rate (%)',
              font: { family: 'Ubuntu' },
            },
            grid: { color: '#f3f4f6' },
            ticks: { font: { family: 'Ubuntu' } },
          },
          y: {
            grid: { display: false },
            ticks: { font: { family: 'Ubuntu', size: 11 } },
          },
        },
      },
    };

    this.coldStartChart = new Chart(ctx, config);
  }

  getFunctionsWithColdStarts(): FunctionResourceMetrics[] {
    return this.resources?.functions.filter((f) => f.coldStarts > 0) ?? [];
  }

  getTotalExecutions(): number {
    return this.functions.reduce((sum, f) => sum + f.totalExecutions, 0);
  }

  getAverageSuccessRate(): number {
    if (this.functions.length === 0) return 0;
    const totalSuccessRate = this.functions.reduce(
      (sum, f) => sum + f.successRate,
      0,
    );
    return totalSuccessRate / this.functions.length;
  }
}
