import { CommonModule } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  ElementRef,
  inject,
  OnInit,
  ViewChild,
  AfterViewInit,
} from '@angular/core';
import { FormsModule } from '@angular/forms';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

import { CardModule } from 'primeng/card';
import { SelectChangeEvent, SelectModule } from 'primeng/select';
import { ButtonModule } from 'primeng/button';
import { DataViewModule } from 'primeng/dataview';
import { formatDistanceStrict, parseISO } from 'date-fns';

import { Chart, ChartConfiguration, registerables } from 'chart.js';
import 'chartjs-adapter-date-fns';

import {
  AppInsightsAlert,
  AlertsSummary,
  ErrorTimelineSummary,
} from '../../../models';
import { RealSystemHealthService } from '../../../../../core/services/real-system-health.service';
import { TimeAgoPipe } from '../../../../../shared/pipes/time-ago.pipe';

Chart.register(...registerables);

@Component({
  selector: 'app-errors-and-alerts',
  imports: [
    CommonModule,
    CardModule,
    SelectModule,
    ButtonModule,
    FormsModule,
    DataViewModule,
    TimeAgoPipe,
  ],
  templateUrl: './errors-and-alerts.component.html',
  styleUrl: './errors-and-alerts.component.scss',
})
export class ErrorsAndAlertsComponent implements OnInit, AfterViewInit {
  private readonly healthService = inject(RealSystemHealthService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);

  @ViewChild('errorTimelineCanvas')
  errorTimelineCanvas!: ElementRef<HTMLCanvasElement>;

  alerts: AppInsightsAlert[] = [];
  alertsSummary: AlertsSummary | null = null;
  errorTimeline: ErrorTimelineSummary | null = null;
  loading = false;
  chartLoading = false;

  private chart: Chart | null = null;

  // Pagination
  rows = 10; // Items per page
  first = 0; // Starting index

  // Sorting
  sortField = '!timestamp'; // Default sort value for the dropdown
  sortOrder = -1; // -1 = descending (newest first)
  sortOptions = [
    { label: 'Newest First', value: '!timestamp' },
    { label: 'Oldest First', value: 'timestamp' },
    { label: 'Severity (High to Low)', value: '!severity' },
    { label: 'Most Occurrences', value: '!count' },
    { label: 'Most Unique Issues', value: '!uniqueIssues' },
    { label: 'Active First', value: '!status' },
  ];

  // Time range selector
  timeRanges = [6, 12, 24, 48]; // Hours
  timeRangeOptions = this.timeRanges.map((range) => ({
    label: range + 'h',
    value: range,
  }));
  selectedTimeRange = 6; // Default to 6 hours for alerts

  // Filter options
  severityFilter = 'all';
  severityOptions = [
    { label: 'All Severities', value: 'all' },
    { label: 'Critical', value: 'Critical' },
    { label: 'Error', value: 'Error' },
    { label: 'Warning', value: 'Warning' },
    { label: 'Information', value: 'Information' },
  ];

  ngOnInit() {
    this.loadAlerts();
    this.loadErrorTimeline();

    // Listen for real-time alert updates via WebSocket
    this.healthService.systemHealth$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((healthData) => {
        if (healthData?.alerts) {
          this.alertsSummary = healthData.alerts;
          this.alerts = this.sortAlerts(healthData.alerts.alerts);
          this.cdr.detectChanges();
        }
      });
  }

  ngAfterViewInit() {
    // Chart will be created after data is loaded in loadErrorTimeline
    if (this.errorTimeline !== null) {
      this.createOrUpdateChart();
    }
  }

  loadAlerts(): void {
    this.loading = true;
    this.healthService.fetchAlerts(`${this.selectedTimeRange}h`).subscribe({
      next: (alertsSummary: AlertsSummary | null) => {
        this.alertsSummary = alertsSummary;
        this.alerts = this.sortAlerts(alertsSummary?.alerts ?? []);
        this.loading = false;
        this.cdr.detectChanges();
      },
      error: (error: unknown) => {
        console.error('Error loading alerts:', error);
        this.loading = false;
        this.cdr.detectChanges();
      },
    });
  }

  onTimeRangeChange(event: SelectChangeEvent): void {
    this.selectedTimeRange = +event.value;
    this.first = 0; // Reset to first page
    this.loadAlerts();
  }

  onSeverityFilterChange(event: SelectChangeEvent): void {
    this.severityFilter = event.value as string;
    this.first = 0; // Reset to first page when filtering
  }

  onSortChange(event: SelectChangeEvent): void {
    const value = event.value as string;

    if (value.startsWith('!')) {
      this.sortOrder = -1;
      this.sortField = value.substring(1);
    } else {
      this.sortOrder = 1;
      this.sortField = value;
    }

    this.alerts = this.sortAlerts(this.alerts);
  }

  sortAlerts(alerts: AppInsightsAlert[]): AppInsightsAlert[] {
    const sorted = [...alerts];

    // Extract actual field name (remove ! if present)
    const fieldName = this.sortField.startsWith('!')
      ? this.sortField.substring(1)
      : this.sortField;

    sorted.sort((a, b) => {
      let result: number;

      switch (fieldName) {
        case 'timestamp':
          result =
            new Date(a.timestamp).getTime() - new Date(b.timestamp).getTime();
          break;
        case 'severity':
          result =
            this.getSeverityWeight(a.severity) -
            this.getSeverityWeight(b.severity);
          break;
        case 'count':
          result = a.count - b.count;
          break;
        case 'uniqueIssues':
          result = a.uniqueIssues - b.uniqueIssues;
          break;
        case 'status':
          result =
            this.getStatusWeight(a.status) - this.getStatusWeight(b.status);
          break;
        default:
          return 0;
      }

      return result * this.sortOrder;
    });

    return sorted;
  }

  getSeverityWeight(severity: string): number {
    switch (severity) {
      case 'Critical':
        return 4;
      case 'Error':
        return 3;
      case 'Warning':
        return 2;
      case 'Information':
        return 1;
      default:
        return 0;
    }
  }

  getStatusWeight(status: string): number {
    switch (status) {
      case 'Active':
        return 3;
      case 'Recent':
        return 2;
      case 'Resolved':
        return 1;
      default:
        return 0;
    }
  }

  getFilteredAlerts(): AppInsightsAlert[] {
    if (this.severityFilter === 'all') {
      return this.alerts;
    }
    return this.alerts.filter(
      (alert) => alert.severity === this.severityFilter,
    );
  }

  // App Insights alerts are just alerts - no "resolved" state like Azure Monitor alerts
  // We just show all alerts within the time range
  getAllAlerts(): AppInsightsAlert[] {
    return this.getFilteredAlerts();
  }

  getSeverityIcon(severity: string): string {
    switch (severity) {
      case 'Critical':
        return 'pi-times-circle';
      case 'Error':
        return 'pi-exclamation-circle';
      case 'Warning':
        return 'pi-exclamation-triangle';
      case 'Information':
        return 'pi-info-circle';
      default:
        return 'pi-info-circle';
    }
  }

  getSeverityColorClass(severity: string): string {
    switch (severity) {
      case 'Critical':
        return 'text-red-600';
      case 'Error':
        return 'text-orange-600';
      case 'Warning':
        return 'text-yellow-600';
      case 'Information':
        return 'text-blue-600';
      default:
        return 'text-gray-600';
    }
  }

  getSeverityBadgeClass(severity: string): string {
    switch (severity) {
      case 'Critical':
        return 'bg-red-100 text-red-800';
      case 'Error':
        return 'bg-orange-100 text-orange-800';
      case 'Warning':
        return 'bg-yellow-100 text-yellow-800';
      case 'Information':
        return 'bg-blue-100 text-blue-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  }

  getAlertBorderClass(severity: string): string {
    switch (severity) {
      case 'Critical':
        return 'border-red-500';
      case 'Error':
        return 'border-orange-500';
      case 'Warning':
        return 'border-yellow-500';
      case 'Information':
        return 'border-blue-500';
      default:
        return 'border-gray-500';
    }
  }

  getAlertDuration(firstOccurrence: string, lastOccurrence: string): string {
    try {
      const first = parseISO(firstOccurrence);
      const last = parseISO(lastOccurrence);
      return formatDistanceStrict(first, last);
    } catch {
      return 'N/A';
    }
  }

  getStatusBadgeClass(status: string): string {
    switch (status) {
      case 'Active':
        return 'bg-red-100 text-red-800 border border-red-200';
      case 'Recent':
        return 'bg-orange-100 text-orange-800 border border-orange-200';
      case 'Resolved':
        return 'bg-green-100 text-green-800 border border-green-200';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  }

  getStatusIcon(status: string): string {
    switch (status) {
      case 'Active':
        return 'pi-exclamation-circle';
      case 'Recent':
        return 'pi-clock';
      case 'Resolved':
        return 'pi-check-circle';
      default:
        return 'pi-info-circle';
    }
  }

  refreshAlerts(): void {
    this.loadAlerts();
    this.loadErrorTimeline();
  }

  loadErrorTimeline(): void {
    this.chartLoading = true;
    this.healthService
      .fetchErrorTimelineData(`${this.selectedTimeRange}h`)
      .subscribe({
        next: (timeline: ErrorTimelineSummary | null) => {
          this.errorTimeline = timeline;
          this.chartLoading = false;
          this.cdr.detectChanges();

          // Create or update chart after data is loaded and view is initialized
          const canvasEl = this.errorTimelineCanvas?.nativeElement;
          if (canvasEl !== undefined) {
            this.createOrUpdateChart();
          }
        },
        error: (err) => {
          console.error('Error loading error timeline:', err);
          this.chartLoading = false;
          this.cdr.detectChanges();
        },
      });
  }

  private createOrUpdateChart(): void {
    const canvasEl = this.errorTimelineCanvas?.nativeElement;
    if (!this.errorTimeline || canvasEl === undefined) {
      return;
    }

    const ctx = canvasEl.getContext('2d');

    if (!ctx) {
      return;
    }

    // Destroy existing chart
    if (this.chart) {
      this.chart.destroy();
    }

    // Group data by service for chart datasets
    const serviceColors: Record<string, string> = {
      'Azure Functions': '#3b82f6',
      'Cosmos DB': '#8b5cf6',
      PostgreSQL: '#06b6d4',
      'Blob Storage': '#10b981',
      'Redis Cache': '#f59e0b',
      'Event Hub': '#ef4444',
      'Azure Table Storage': '#ec4899',
    };

    const datasets = this.errorTimeline.services
      .map((service) => {
        const timeline = this.errorTimeline;
        if (!timeline) {
          return null;
        }

        const serviceData = timeline.dataPoints
          .filter((dp) => dp.service === service)
          .map((dp) => ({
            x: new Date(dp.timestamp).getTime(),
            y: dp.errorCount,
          }));

        return {
          label: service,
          data: serviceData,
          borderColor: serviceColors[service] ?? '#6b7280',
          backgroundColor: (serviceColors[service] ?? '#6b7280') + '20',
          borderWidth: 2,
          tension: 0.4,
          fill: true,
        };
      })
      .filter(
        (dataset): dataset is NonNullable<typeof dataset> => dataset !== null,
      );

    const config: ChartConfiguration<'line'> = {
      type: 'line',
      data: {
        datasets,
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        interaction: {
          mode: 'index',
          intersect: false,
        },
        plugins: {
          legend: {
            position: 'bottom',
            labels: {
              usePointStyle: true,
              padding: 15,
              font: {
                family: 'Ubuntu',
              },
            },
          },
          tooltip: {
            callbacks: {
              title: (items) => {
                const xValue = items[0]?.parsed?.x;
                if (xValue == null) {
                  return '';
                }
                const date = new Date(xValue);
                return date.toLocaleString();
              },
              label: (context) => {
                const label = context.dataset.label ?? '';
                const value = context.parsed.y;
                return `${label}: ${value} error${value === 1 ? '' : 's'}`;
              },
            },
          },
        },
        scales: {
          x: {
            type: 'time',
            time: {
              unit: 'minute',
              displayFormats: {
                minute: 'HH:mm',
                hour: 'MMM d, HH:mm',
              },
            },
            title: {
              display: true,
              text: 'Time',
              font: {
                family: 'Ubuntu',
              },
            },
            grid: {
              display: true,
              color: '#f3f4f6',
            },
            ticks: {
              font: {
                family: 'Ubuntu',
              },
            },
          },
          y: {
            beginAtZero: true,
            title: {
              display: true,
              text: 'Error Count',
              font: {
                family: 'Ubuntu',
              },
            },
            ticks: {
              precision: 0,
              font: {
                family: 'Ubuntu',
              },
            },
            grid: {
              color: '#f3f4f6',
            },
          },
        },
      },
    };

    this.chart = new Chart(ctx, config);
  }
}
