import { CommonModule } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  DestroyRef,
  inject,
  OnInit,
} from '@angular/core';
import { FormsModule } from '@angular/forms';

import { CardModule } from 'primeng/card';
import { ChartModule } from 'primeng/chart';
import { TableModule } from 'primeng/table';
import { TagModule } from 'primeng/tag';
import { ProgressBarModule } from 'primeng/progressbar';
import { ButtonModule } from 'primeng/button';

import { SystemHealthData } from '../../../models';
import { SystemHealthBroadcastService } from '../../../services/system-health-broadcast.service';
import { SystemHealthService } from '../../../services/system-health.service';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { SelectChangeEvent, SelectModule } from 'primeng/select';

type TagSeverity =
  | 'success'
  | 'warn'
  | 'danger'
  | 'info'
  | 'secondary'
  | 'contrast'
  | null
  | undefined;

@Component({
  selector: 'app-system-overview',
  imports: [
    CommonModule,
    TableModule,
    TagModule,
    CardModule,
    ProgressBarModule,
    ChartModule,
    ButtonModule,
    SelectModule,
    FormsModule,
  ],
  templateUrl: './system-overview.component.html',
  styleUrl: './system-overview.component.scss',
})
export class SystemOverviewComponent implements OnInit {
  private readonly broadcastService = inject(SystemHealthBroadcastService);
  private readonly healthService = inject(SystemHealthService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);
  healthData?: SystemHealthData;
  connected = true;

  // Flattened data for easy display - using Record to allow flexible backend data
  services: Record<string, unknown>[] = [];
  functions: Record<string, unknown>[] = [];

  // Time range selector
  timeRanges = [6, 12, 24]; // Hours
  timeRangeOptions = this.timeRanges.map((range) => ({
    label: range + 'h',
    value: range,
  }));
  selectedTimeRange = 6; // Default to 6 hours

  ngOnInit() {
    this.healthService.initializeHealthListeners();

    // Listen for health updates
    this.broadcastService
      .onHealthUpdate()
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((update) => {
        this.healthData = update.data;
        this.processHealthData();
        this.cdr.detectChanges();
      });

    // Listen for connection status
    this.broadcastService
      .onWebSocketStatusChange()
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((status) => {
        this.connected = status.connected;
      });
  }

  private processHealthData() {
    if (!this.healthData) return;

    // Services from overview - cast to Record for flexible access
    this.services = (this.healthData.overview?.services ??
      []) as unknown as Record<string, unknown>[];

    // Functions
    this.functions = (this.healthData.azureFunctions ??
      []) as unknown as Record<string, unknown>[];
  }

  getStatusSeverity(status: string): TagSeverity {
    switch (status) {
      case 'healthy':
        return 'success';
      case 'warning':
        return 'warn';
      case 'critical':
        return 'danger';
      default:
        return 'info';
    }
  }

  getSeverityTag(severity: string): TagSeverity {
    switch (severity?.toLowerCase()) {
      case 'critical':
        return 'danger';
      case 'error':
        return 'danger';
      case 'warning':
        return 'warn';
      case 'information':
        return 'info';
      default:
        return 'info';
    }
  }

  getHealthyServicesCount(): number {
    return this.services.filter(
      (s: Record<string, unknown>) =>
        String(s['status']).toLowerCase() === 'healthy',
    ).length;
  }

  getTotalServicesCount(): number {
    return this.services.length;
  }

  getAverageErrorRate(): number {
    if (this.services.length === 0) return 0;
    const totalErrorRate = this.services.reduce(
      (sum: number, s: Record<string, unknown>) =>
        sum + (Number(s['errorRatePercent']) || 0),
      0,
    );
    return totalErrorRate / this.services.length;
  }

  getAverageResponseTime(): number {
    if (this.services.length === 0) return 0;
    const totalResponseTime = this.services.reduce(
      (sum: number, s: Record<string, unknown>) =>
        sum + (Number(s['avgResponseMs']) || 0),
      0,
    );
    return totalResponseTime / this.services.length;
  }

  selectTimeRange(hours: number): void {
    this.selectedTimeRange = hours;
    // Trigger data refresh with the selected time range
    this.healthService.refreshData(`${hours}h`);
    this.cdr.detectChanges();
  }

  onTimeRangeChange(event: SelectChangeEvent): void {
    this.selectTimeRange(+event.value);
  }
}
