import { Injectable, inject } from '@angular/core';
import { BroadcastEventMessageService } from '../../../features/administration/services/broadcast-event-messages.service';
import { SystemHealthData } from '../models';

@Injectable({
  providedIn: 'root',
})
export class SystemHealthBroadcastService {
  private readonly broadcastService = inject(BroadcastEventMessageService);

  // Convenience methods for system health events
  broadcastHealthUpdate(data: SystemHealthData): void {
    this.broadcastService.broadcast('system-health-update', {
      data,
      timestamp: new Date().toISOString(),
    });
  }

  broadcastWebSocketStatus(connected: boolean, reason?: string): void {
    this.broadcastService.broadcast('websocket-status-change', {
      connected,
      reason,
    });
  }

  broadcastServiceAlert(
    serviceName: string,
    severity: 'warning' | 'error' | 'critical',
    message: string,
  ): void {
    this.broadcastService.broadcast('health-service-alert', {
      serviceName,
      severity,
      message,
    });
  }

  // Subscription methods
  onHealthUpdate() {
    return this.broadcastService.on('system-health-update');
  }

  onWebSocketStatusChange() {
    return this.broadcastService.on('websocket-status-change');
  }

  onServiceAlert() {
    return this.broadcastService.on('health-service-alert');
  }

  onDashboardRefreshRequested(source?: string) {
    return this.broadcastService.broadcast('dashboard-refresh-requested', {
      source: source ?? 'manual-refresh',
    });
  }
}
